/* eslint no-param-reassign: 0 */
/* eslint no-unused-vars: 0 */
/* eslint no-console: 0 */

import screenfull from 'screenfull'
import util from '@/libs/util.js'
import db from '@/libs/db.js'
import themeList from '@/assets/style/theme/list.js'
import utilIce from "../../libs/util-ice";

const pageOpenedDefult = {
  name: 'index',
  meta: {
    title: ''
  }
}

export default {
  state: {
    //id
    captchaId:'',
    //
    codeImgUrl:'',
    // 
    userInfo: {
      name: ''
    },
    //
    menuData:{},
    // 
    menuHeader: [],
    // 
    menuAside: [],
    // 
    isFullScreen: false,
    // 
    isGrayMode: false,
    // 
    isMenuAsideCollapse: false,
    // 
    themeList,
    // 
    themeActiveName: themeList[4].name, //  
    //  tab 
    pagePool: [],
    // 
    pageOpenedList: [
      pageOpenedDefult
    ],
    // 
    pageCurrent: '',
    //  UA
    ua: {},
    //
    userAuthority: {}
  },
  getters: {
    /**
     * @description   
     * @param {vuex state} state vuex state
     */
    d2adminThemeActiveSetting (state) {
      return state.themeList.find(theme => theme.name === state.themeActiveName)
    },
    /**
     * @description  name
     * @param {*} state vuex state
     */
    d2adminKeepAliveInclude (state) {
      return state.pageOpenedList.filter((item) => {
        if (item.meta) {
          if (item.meta.notCache) {
            return false
          }
        }
        return true
      }).map(e => e.name)
    }
  },
  actions: {
    startLogin({state, commit, rootState}, {vm}){
      commit('getCodeId', vm)
    },
    /**
     * 
     * @param {Object} param0 context
     * @param {Object} param1 { vue, username, password }
     */
    d2adminLogin ({state, commit, rootState}, {vm, userName, password, captcha, checkId, token}) {
      let loginData ={
        userName,
        password,
        captcha,
        checkId
      };
      // 
      vm.$axios({
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
          'x-auth-token': token
        },
        url: vm.$API.URL + vm.$API.API.login,
        params: loginData
      })
        .then((res) => {
          if(res.code === '6000'){
            vm.$message.error('');
            setTimeout(function(){
              commit('getCodeId', vm);
            },400)
          } else if(res.code === '1000'){
            state.userAuthority = res.data;
            //////////////////////////////////
            state.menuData = res.data.modulePermissions;
            state.menuAside = [];
            for(let item in state.menuData){
              let tempMenu = {
                title: '',
                name: '',
                icon: 'folder-o',
                children: []
              }
              tempMenu.title = state.menuData[item].showName;
              tempMenu.name = state.menuData[item].showName;
              for(let i=0; i<state.menuData[item].modulePermissionBoList.length; i++){
                let tempchild = {
                  title:'',
                  name:'',
                  path:''
                }
                tempchild.title = state.menuData[item].modulePermissionBoList[i].showName;
                tempchild.name = state.menuData[item].modulePermissionBoList[i].showName;
                tempchild.path = state.menuData[item].modulePermissionBoList[i].pageUrl;
                tempMenu.children.push(tempchild);
              }
              state.menuAside.push(tempMenu)
            }
            if(state.menuData !== ''){
              util.cookies.set('d2adminMenuAside', state.menuAside); //cookie
            }
            commit('d2adminMenuAsideSet', JSON.parse(util.cookies.get('d2adminMenuAside')));
            /////////////////////////////////

            //  cookie  uuid  token  cookie
            // 
            // uuid    
            util.cookies.set('uuid', res.data.userName)
            util.cookies.set('token', res.data.token)
            //  vuex 
            commit('d2adminUserInfoSet', {
              name: res.data.userName
            })
            // 
            commit('d2adminLoginSuccessLoad')

            // 
            vm.$router.push({
              name: 'index'
            })
          } else {
            vm.$message.error(res.message);
            setTimeout(function(){
              commit('getCodeId', vm);
            },400)
          }
        })
        .catch((err) => {
          console.group('');
          console.log('err: ', err);
          console.groupEnd();
          vm.$message.error(err.message);
        })
    },
    /**
     * 
     * @param {Object} param0 context
     * @param {Object} confirm need confirm ?
     */
    d2adminLogout ({ state, commit, rootState }, { vm, confirm }) {
      /**
       * @description 
       */
      function logout () {
        // cookie
        util.cookies.remove('token');
        // util.cookies.remove('uuid');
        util.cookies.remove('d2adminMenuAside');
        // 
        vm.$router.push({
          name: 'login'
        })
      }
      // 
      if (confirm) {
        commit('d2adminGrayModeSet', true)
        vm.$confirm('?  ', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            // 
            vm.$axios({
              method: 'post',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              },
              url: vm.$API.URL + vm.$API.API.accountInfo
            }).then((res) => {
              // console.log(res)
            }).catch((err) => {
              console.log(err)
            })
            commit('d2adminGrayModeSet', false)
            logout()
          })
          .catch(() => {
            commit('d2adminGrayModeSet', false)
            vm.$message('')
          })
      } else {
        logout()
      }
    }
  },
  mutations: {
    /**
     * 
     */
    getCodeId(state,vm){
      vm.$axios({
        method: 'get',
        url: vm.$API.URL + '/kaptcha/captcha/getCaptchaId',
        data: {}
      }).then((res) => {
          state.captchaId = res.captchaId;
          state.codeImgUrl = vm.$API.URL + '/kaptcha/captcha.jpg?captchaId=' + res.captchaId;
      }).catch((err) => {
        console.log(err)
      })
    },
    /**
     * @class 
     * @description  state    uuid
     * @param {vuex state} state vuex state
     * @param {String} key key name
     */
    d2adminUtilVuex2DbByUuid (state, key) {
      const row = db.get(key).find({ uuid: util.cookies.get('uuid') })
      if (row.value()) {
        row.assign({ value: state[key] }).write()
      } else {
        db.get(key).push({
          uuid: util.cookies.get('uuid'),
          value: state[key]
        }).write()
      }
    },
    /**
     * @class 
     * @description  vuex  uuid
     * @param {vuex state} state vuex state
     * @param {Object} param1 key and default value
     */
    d2adminUtilDb2VuexByUuid (state, { key, defaultValue }) {
      const row = db.get(key).find({ uuid: util.cookies.get('uuid') }).value()
      state[key] = row ? row.value : defaultValue
    },
    /**
     * @class 
     * @description  state    uuid 
     * @param {vuex state} state vuex state
     * @param {String} key key name
     */
    d2adminUtilVuex2Db (state, key) {
      const row = db.get(key).find({ pub: 'pub' })
      if (row.value()) {
        row.assign({ value: state[key] }).write()
      } else {
        db.get(key).push({
          pub: 'pub',
          value: state[key]
        }).write()
      }
    },
    /**
     * @class 
     * @description  vuex  uuid 
     * @param {vuex state} state vuex state
     * @param {Object} param1 key and default value
     */
    d2adminUtilDb2Vuex (state, { key, defaultValue }) {
      const row = db.get(key).find({ pub: 'pub' }).value()
      state[key] = row ? row.value : defaultValue
    },
    /**
     * @class 
     * @description   
     * @param {vuex state} state vuex state
     * @param {Function} fn function
     */
    d2adminUtilDatabaseUser (state, fn) {
      const uuid = util.cookies.get('uuid')
      const database = db.get('database').find({ uuid })
      if (database.value() === undefined) {
        db.get('database').push({
          uuid,
          value: {}
        }).write()
        if (fn) {
          fn(db.get('database').find({ uuid }).get('value'))
        }
      } else if (fn) {
        fn(database.get('value'))
      }
    },
    /**
     * @class 
     * @description    d2adminUtilDatabaseUser 
     * @param {vuex state} state vuex state
     */
    d2adminUtilDatabaseUserClear () {
      db.get('database')
        .remove({ uuid: util.cookies.get('uuid') })
        .write()
    },
    /**
     * @class 
     * @description  
     * @param {vuex state} state vuex state
     * @param {Function} fn function
     */
    d2adminUtilDatabase (state, fn) {
      if (fn) {
        fn(db.get('databasePublic'))
      }
    },
    /**
     * @class 
     * @description  
     * @param {vuex state} state vuex state
     */
    d2adminUtilDatabaseClear () {
      db.set('databasePublic', {})
        .write()
    },
    /**
     * @class UA
     * @description  UA
     * @param {vuex state} state vuex state
     */
    d2adminUaGet (state) {
      state.ua = util.ua()
    },
    /**
     * @class menuHeader
     * @description 
     * @param {vuex state} state vuex state
     * @param {Array} menu menu setting
     */
    d2adminMenuHeaderSet (state, menu) {
      state.menuHeader = menu
    },
    /**
     * @class menuAside
     * @description 
     * @param {vuex state} state vuex state
     * @param {Array} menu menu setting
     */
    d2adminMenuAsideSet (state, menu) {
      state.menuAside = menu
    },
    /**
     * @class ...
     * @description 
     * @param {vuex state} state vuex state
     */
    d2adminLoginSuccessLoad (state) {
      // DB -> store 
      this.commit('d2adminUserInfoLoad')
      // DB -> store 
      this.commit('d2adminThemeLoad')
      // DB -> store 
      this.commit('d2adminPageOpenedListLoad')  //Julia - 
      // DB -> store 
      this.commit('d2adminMenuAsideCollapseLoad') //Julia - 
    },
    /**
     * @description 
     * @class userInfo
     * @param {vuex state} state vuex state
     * @param {String} userInfo userInfo
     */
    d2adminUserInfoSet (state, userInfo) {
      state.userInfo = userInfo
      this.commit('d2adminUtilVuex2DbByUuid', 'userInfo')
    },
    /**
     * @description 
     * @class userInfo
     * @param {vuex state} state vuex state
     */
    d2adminUserInfoLoad () {
      this.commit('d2adminUtilDb2VuexByUuid', {
        key: 'userInfo',
        defaultValue: {
          name: ''
        }
      })
    },
    /**
     * @class pagePool
     * @description  pagePool ()
     * @param {vuex state} state vuex state
     * @param {Array} pagePool tags
     */
    d2adminPagePoolSet (state, pagePool) {
      state.pagePool = pagePool
    },
    /**
     * @class pageCurrent
     * @description 
     * @param {vuex state} state vuex state
     * @param {Object} param1 { name, params, query } 
     */
    d2adminPageOpenNew (state, { name, params, query }) {
      // 
      const { pageOpenedList } = state
      //  
      let pageOpendIndex = 0
      const pageOpend = pageOpenedList.find((page, index) => {
        const same = page.name === name
        pageOpendIndex = same ? index : pageOpendIndex
        return same
      })
      if (pageOpend) {
        //   name 
        this.commit('d2adminPageOpenedListUpdateItem', { index: pageOpendIndex, params, query })
      } else {
        // 
        const tag = state.pagePool.find(t => t.name === name)
        if (tag) {
          this.commit('d2adminTagIncreate', { tag, params, query })
        }
      }
      this.commit('d2adminPageCurrentSet', name)
    },
    /**
     * @class pageCurrent
     * @description  name
     * @param {vuex state} state vuex state
     * @param {String} name new name
     */
    d2adminPageCurrentSet (state, name) {
      state.pageCurrent = name
    },
    /**
     * @class pageOpenedList
     * @description 
     * @param {vuex state} state vuex state
     * @param {Object} param1 { index, params, query } 
     */
    d2adminPageOpenedListUpdateItem (state, { index, params, query }) {
      // 
      const page = state.pageOpenedList[index]
      page.params = params || page.params
      page.query = query || page.query
      state.pageOpenedList.splice(index, 1, page)
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'pageOpenedList')
    },
    /**
     * @class pageOpenedList
     * @description 
     * @param {vuex state} state vuex state
     */
    d2adminPageOpenedListLoad () {
      this.commit('d2adminUtilDb2VuexByUuid', {
        key: 'pageOpenedList',
        defaultValue: [
          pageOpenedDefult
        ]
      })
    },
    /**
     * @class pageOpenedList
     * @description  tag ()
     * @param {vuex state} state vuex state
     * @param {Object} param1 new tag info
     */
    d2adminTagIncreate (state, { tag, params, query }) {
      //  tag 
      const newPage = tag
      newPage.params = params || newPage.params
      newPage.query = query || newPage.query
      // 
      state.pageOpenedList.push(newPage)
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'pageOpenedList')
    },
    /**
     * @class pageOpenedList
     * @description  tag ()
     * @param {vuex state} state vuex state
     * @param {Object} param1 { tagName: , vm: vue }
     */
    d2adminTagClose (state, { tagName, vm }) {
      // 
      let newPage = state.pageOpenedList[0]
      const isCurrent = state.pageCurrent === tagName
      // 
      if (isCurrent) {
        // 
        const len = state.pageOpenedList.length
        for (let i = 1; i < len; i += 1) {
          if (state.pageOpenedList[i].name === tagName) {
            if (i < len - 1) {
              newPage = state.pageOpenedList[i + 1]
            } else {
              newPage = state.pageOpenedList[i - 1]
            }
            break
          }
        }
      }
      // 
      const index = state.pageOpenedList.findIndex(page => page.name === tagName)
      if (index >= 0) {
        state.pageOpenedList.splice(index, 1)
      }
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'pageOpenedList')
      // 
      if (isCurrent) {
        const { name = '', params = {}, query = {} } = newPage
        const routerObj = {
          name,
          params,
          query
        }
        vm.$router.push(routerObj)
      }
    },
    /**
     * @class pageOpenedList
     * @description 
     * @param {vuex state} state vuex state
     */
    d2adminTagCloseLeft (state) {
      let currentIndex = 0
      state.pageOpenedList.forEach((page, index) => {
        if (page.name === state.pageCurrent) {
          currentIndex = index
        }
      })
      if (currentIndex > 0) {
        state.pageOpenedList.splice(1, currentIndex - 1)
      }
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'pageOpenedList')
    },
    /**
     * @class pageOpenedList
     * @description 
     * @param {vuex state} state vuex state
     */
    d2adminTagCloseRight (state) {
      let currentIndex = 0
      state.pageOpenedList.forEach((page, index) => {
        if (page.name === state.pageCurrent) {
          currentIndex = index
        }
      })
      state.pageOpenedList.splice(currentIndex + 1)
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'pageOpenedList')
    },
    /**
     * @class pageOpenedList
     * @description  tag
     * @param {vuex state} state vuex state
     */
    d2adminTagCloseOther (state) {
      let currentIndex = 0
      state.pageOpenedList.forEach((page, index) => {
        if (page.name === state.pageCurrent) {
          currentIndex = index
        }
      })
      if (currentIndex === 0) {
        state.pageOpenedList.splice(1)
      } else {
        state.pageOpenedList.splice(currentIndex + 1)
        state.pageOpenedList.splice(1, currentIndex - 1)
      }
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'pageOpenedList')
    },
    /**
     * @class pageOpenedList
     * @description  tag
     * @param {vuex state} state vuex state
     * @param {Object} vm vue
     */
    d2adminTagCloseAll (state, vm) {
      state.pageOpenedList.splice(1)
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'pageOpenedList')
      // 
      if (vm.$route.name !== 'index') {
        vm.$router.push({
          name: 'index'
        })
      }
    },
    /**
     * 
     * @class isMenuAsideCollapse
     * @param {vuex state} state vuex state
     * @param {Boolean} collapse is collapse
     */
    d2adminMenuAsideCollapseSet (state, collapse) {
      state.isMenuAsideCollapse = collapse
      this.commit('d2adminUtilVuex2DbByUuid', 'isMenuAsideCollapse')
    },
    /**
     * 
     * @class isMenuAsideCollapse
     * @param {vuex state} state vuex state
     */
    d2adminMenuAsideCollapseToggle (state) {
      state.isMenuAsideCollapse = !state.isMenuAsideCollapse
      this.commit('d2adminUtilVuex2DbByUuid', 'isMenuAsideCollapse')
    },
    /**
     * 
     * @class isMenuAsideCollapse
     * @param {vuex state} state vuex state
     */
    d2adminMenuAsideCollapseLoad () {
      this.commit('d2adminUtilDb2VuexByUuid', {
        key: 'isMenuAsideCollapse',
        defaultValue: false
      })
    },
    /**
     * @class isFullScreen
     * @description 
     * @param {vuex state} state vuex state
     */
    d2adminFullScreenToggle () {
      if (screenfull.isFullscreen) {
        screenfull.exit()
        this.commit('d2adminFullScreenSet', false)
      } else {
        screenfull.request()
        this.commit('d2adminFullScreenSet', true)
      }
    },
    /**
     * @class isFullScreen
     * @description  store 
     * @param {vuex state} state vuex state
     */
    d2adminFullScreenSet (state, isFullScreen) {
      state.isFullScreen = isFullScreen
    },
    /**
     * @class isGrayMode
     * @description 
     * @param {vuex state} state vuex state
     */
    d2adminGrayModeToggle (state) {
      state.isGrayMode = !state.isGrayMode
    },
    /**
     * @class isGrayMode
     * @description 
     * @param {vuex state} state vuex state
     * @param {Boolean} value new value
     */
    d2adminGrayModeSet (state, value) {
      state.isGrayMode = value
    },
    /**
     * @class themeActiveName
     * @description dom
     * @param {vuex state} state vuex state
     * @param {String} themeValue 
     */
    d2adminThemeSet (state, themeName) {
      // 
      const theme = state.themeList.find(e => e.name === themeName)
      if (theme) {
        //  state
        state.themeActiveName = themeName
      } else {
        // 
        state.themeActiveName = state.themeList[4].name // 
      }
      //  vuex  dom
      this.commit('d2adminTheme2dom')
      // 
      this.commit('d2adminUtilVuex2DbByUuid', 'themeActiveName')
    },
    /**
     * @class themeActiveName
     * @description 
     * @param {vuex state} state vuex state
     */
    d2adminThemeLoad (state) {
      this.commit('d2adminUtilDb2VuexByUuid', {
        key: 'themeActiveName',
        defaultValue: state.themeList[4].name
      })
      this.commit('d2adminTheme2dom')
    },
    /**
     * @class themeActiveName
     * @description  vuex  dom
     * @param {vuex state} state vuex state
     */
    d2adminTheme2dom (state) {
      document.body.className = `theme-${state.themeActiveName}`
    }
  }
}
