<template>
  <div class="login-page">
    <div class="layer bg" id="login"></div>
    <div class="layer flex-center">
      <!-- logo -->
      <div class="logo-group">
        <img src="./image/login-logo.png" alt="logo">
      </div>
      <!--  -->
      <div class="form-group">
        <el-card>
          <el-form ref="loginForm" label-position="top" :rules="rules" :model="formLogin">
            <el-form-item  prop="username">
              <el-input type="text" v-model="formLogin.username" placeholder="">
                <i slot="prepend" class="fa fa-user-circle-o"></i>
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" v-model="formLogin.password" placeholder="">
                <i slot="prepend" class="fa fa-keyboard-o"></i>
              </el-input>
            </el-form-item>
            <el-form-item prop="code">
              <el-input type="text" v-model="formLogin.code" placeholder="- - - -">
                <template slot="prepend"></template>
                <template slot="append">
                  <img class="login-code" :src="loginCodeImgUrl" @click="refreshCodeImg">
                </template>
              </el-input>
            </el-form-item>
            <el-button @click="submit(captchaId)" type="primary" class="button-login" id="loginBtn"></el-button>
          </el-form>
          <el-checkbox class="remember-pwd" v-model="pwdChecked"></el-checkbox>
        </el-card>
      </div>
      <!--  -->
      <!--<el-button type="info" class="button-help" @click="dialogVisible = true"></el-button>-->
    </div>
  </div>
</template>

<script>
/* eslint-disable */
require('particles.js')
import util from '@/libs/util.js'
import config from './config/default'
import { mapActions} from 'vuex'
import d2admin from '../../store/modules/d2admin';
export default {
  data () {
    return {
      // 
      // dialogVisible: false,
      // 
      formLogin: {
        username: '',
        password: '',
        code: ''
      },
      // 
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      loginCodeImgUrl: '',
      pwdChecked: false  //
    }
  },
  mounted () {
    // 
    particlesJS('login', config);
    //new
    this.startLogin({
      vm: this
    });

    //
    if(util.cookies.get('pwdChecked') === 'true'){
      if(util.cookies.get('uuid') && util.cookies.get('uuid') !== ''){
        this.formLogin.username = util.cookies.get('uuid');
      } else {
        this.formLogin.username = '';
      }
      if(util.cookies.get('password') && util.cookies.get('password') !== ''){
        this.formLogin.password = util.cookies.get('password');
      } else {
        this.formLogin.password = '';
      }
    } else {
      this.formLogin.username = '';
      this.formLogin.password = '';
    }
    util.cookies.set('pwdChecked',false);
  },
  computed:{
    captchaId(){
      return d2admin.state.captchaId;
    },
    codeImgUrl(){
      return d2admin.state.codeImgUrl;
    }
  },
  watch:{
    'codeImgUrl':function(val){
      this.loginCodeImgUrl = val;
      this.formLogin.code = '';
    },
    'formLogin.password':function(val){  //
      util.cookies.set('password',val)
    },
    'pwdChecked':function(val){  //
      util.cookies.set('pwdChecked',val)
    }
  },
  methods: {
    ...mapActions([
      'd2adminLogin',
      'startLogin'
    ]),
    /**
     * @description 
     * @param {Object} user 
     */
    // handleUserBtnClick (user) {
    //   this.formLogin.username = user.username
    //   this.formLogin.password = user.password
    //   this.submit()
    // },
    /**
     * @description 
     */

    // 
    submit () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // 
          //  
          // 
          this.d2adminLogin({
            vm: this,
            userName: this.formLogin.username,
            password: this.formLogin.password,
            captcha: this.formLogin.code,
            checkId:this.captchaId,
            token:''
          })
        } else {
          // 
          this.$message.error('')
        }
      })
    },
    //
    refreshCodeImg(){
      this.startLogin({
        vm: this
      });
    }
  }
}
</script>

<style lang="scss">
@import './style.scss';
  .remember-pwd{
    margin:15px 0 7px;
    color:#909399;
    font-size:12px;
    .el-checkbox__inner{
      width:12px;
      height:12px;
      &::after{
        height:5px;
        left:3px;
      }
    }
    .el-checkbox__label{
      font-size:12px;
      padding-left:6px;
    }
  }
</style>
