<template>
    <d2-container>
        <template slot="header"> -   <span v-if="mobile">({{mobile}})</span> <i class="el-icon-close detail-close" @click="$router.go(-1)"></i></template>
        <div class="user-detail">
            <div class="title"></div>
            <div class="info-box">
                <p class="info-tit"></p>
                <ul class="info">
                    <li v-for="item,index in userInfo" :key="index">
                        <span>{{item.tit}}</span>
                        <p>{{item.txt}}</p>
                    </li>
                </ul>
            </div>
            <div class="assets-info">
                <el-tabs v-model="activeName" type="border-card"> <!--@tab-click="handleClick"-->
                    <el-tab-pane label="" name="first">
                        <p class="name"></p>
                        <el-table
                                :data="infoTable"
                                highlight-current-row
                                class="assets-table"
                                style="width: 700px">
                            <el-table-column
                                    prop="symbol"
                                    label=""
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    prop="availableBalance"
                                    label=""
                                    width="240">
                            </el-table-column>
                            <el-table-column
                                    prop="blockedBalance"
                                    label="">
                            </el-table-column>
                        </el-table>
                    </el-tab-pane>
                    <el-tab-pane label="" name="second">
                        <el-table
                                highlight-current-row
                                class="assets-table"
                                :data="logTable"
                                style="width:100%;padding:0 24px;">
                            <el-table-column prop="typeDetail" label="" min-width="140"></el-table-column>
                            <el-table-column prop="coinAbbreviation" label="" min-width="90"></el-table-column>
                            <el-table-column prop="operateAmount" label="" min-width="170"></el-table-column>
                            <el-table-column prop="availableBalanceBeforeOperate" label="" min-width="170"></el-table-column>
                            <el-table-column prop="availableBalanceAfterOperate" label="" min-width="170"></el-table-column>
                            <el-table-column prop="blockNumber" label="" min-width="170"></el-table-column>
                            <el-table-column prop="blockedBalanceBeforeOperate" label="" min-width="170"></el-table-column>
                            <el-table-column prop="blockedBalanceAfterOperate" label="" min-width="170"></el-table-column>
                            <el-table-column prop="createTime" label="" min-width="180"></el-table-column>
                        </el-table>
                    </el-tab-pane>
                </el-tabs>
            </div>
            <el-button type="info" plain class="return-btn" @click="$router.go(-1)"></el-button>
        </div>
    </d2-container>
</template>

<script>
  import util from '@/libs/util.js'
  export default {
    name: "userDetail",
    data(){
        return{
          id:'',
          mobile:'',
          activeName: 'first',
          userInfo:[
            {tit: 'ID', txt: ''},
            {tit: '', txt: ''},
            {tit: '', txt: ''},
            {tit: '', txt: ''},
          ],
          infoTable:[],
          logTable:[],
          AssetsLogTable:[],
          paramsData:{
            'coinIds': null,
            'endTime': null,
            'page': 1,
            'pagesize': 10,
            'startTime': null,
            'status': null,
            'type': null,
            'typeDetail': null,
            'userId': null,
            'userMobile': null
          }
        }
    },
    methods:{
      //
      getUserInfo(userId){
        let vm = this;
        this.axiosPOST(vm, vm.$API.API.userAssetsInfo,
          'application/x-www-form-urlencoded',
          { userId },
          vm.getSuccessCB,
          vm.getErrorCB
        )
      },
      //
      getSuccessCB(vm,res){
        if(!res.data || res.data === ''){
          vm.infoTable = [];
        } else {
          vm.setAssetsInfoTable(res.data)
        }
      },
      //
      getErrorCB(err){
        console.log(err)
      },
      //
      setAssetsInfoTable(_data) {
        let myData = [];
        for (let i = 0; i <_data.length; i++) {
          let obj = {};
          obj.availableBalance = _data[i].availableBalance;
          obj.blockedBalance = _data[i].blockedBalance;
          if(!_data[i].symbol || _data[i].symbol === ''){
            obj.symbol = '-';
          } else {
            obj.symbol = _data[i].symbol;
          }
          myData[i] = obj;
        }
        this.infoTable = myData;
      },
      //
      getUserLog(){
        let vm = this;
        this.axiosPOST(vm, vm.$API.API.userAssetsLog,
          'application/json',
          vm.paramsData,
          vm.getAssetsLogSuccessCB,
          vm.getErrorCB
        )
      },
      //
      getAssetsLogSuccessCB(vm,res){
        if(!res.data || res.data === ''){
          vm.logTable = [];
        } else {
          vm.setAssetsLogTable(res.data);
        }
      },
      //
      setAssetsLogTable(_data){
        let myData = [];
        for (let i = 0; i <_data.length; i++) {
          let obj = {};
          switch(_data[i].typeDetail){ //
            case 1:
              obj.typeDetail = '';
              break;
            case 2:
              obj.typeDetail = '';
              break;
            case 3:
              obj.typeDetail = '';
              break;
            case 4:
              obj.typeDetail = '';
              break;
            case 5:
              obj.typeDetail = '';
              break;
            case 6:
              obj.typeDetail = '';
              break;
            case 7:
              obj.typeDetail = '';
              break;
            case 8:
              obj.typeDetail = '';
              break;
            case 9:
              obj.typeDetail = '';
              break;
            case 10:
              obj.typeDetail = '';
              break;
            case 20:
              obj.typeDetail = '';
              break;
            case 21:
              obj.typeDetail = '';
              break;
            default:
              obj.typeDetail = '-'
          }
          obj.coinAbbreviation = _data[i].coinAbbreviation; //
          if(_data[i].operateAmount === null || _data[i].operateAmount === ''){  //
            obj.operateAmount = '-'
          } else {
            obj.operateAmount = _data[i].operateAmount
          }
          if(_data[i].availableBalanceBeforeOperate === null || _data[i].availableBalanceBeforeOperate === ''){  //
            obj.availableBalanceBeforeOperate = '-'
          } else {
            obj.availableBalanceBeforeOperate = _data[i].availableBalanceBeforeOperate
          }
          if(_data[i].availableBalanceAfterOperate === null || _data[i].availableBalanceAfterOperate === ''){  //
            obj.availableBalanceAfterOperate = '-'
          } else {
            obj.availableBalanceAfterOperate = _data[i].availableBalanceAfterOperate
          }
          if(_data[i].blockNumber === null || _data[i].blockNumber === ''){  //
            obj.blockNumber = '-'
          } else {
            obj.blockNumber = _data[i].blockNumber
          }
          if( _data[i].blockedBalanceBeforeOperate === null || _data[i].blockedBalanceBeforeOperate === ''){  //
            obj.blockedBalanceBeforeOperate = '-'
          } else {
            obj.blockedBalanceBeforeOperate = _data[i].blockedBalanceBeforeOperate
          }
          if(_data[i].blockedBalanceAfterOperate === null || _data[i].blockedBalanceAfterOperate === ''){  //
            obj.blockedBalanceAfterOperate = '-'
          } else {
            obj.blockedBalanceAfterOperate = _data[i].blockedBalanceAfterOperate
          }
          if(!_data[i].createTime || _data[i].createTime === ''){  //
            obj.createTime = '-'
          } else {
            obj.createTime = _data[i].createTime
          }
          myData[i] = obj;
        }
        this.logTable = myData;
      },
    },
    mounted(){
      this.paramsData.userId = this.id = this.userInfo[0].txt = this.$route.params.id;
      this.mobile = this.userInfo[1].txt = this.$route.params.mobile;
      this.userInfo[2].txt = this.$route.params.nickname;
      this.userInfo[3].txt = this.$route.params.createTime;
      this.getUserInfo(this.id);
      this.getUserLog();
    }
  }
</script>

<style scoped lang="scss">
    .user-detail{
        width:100%;
        text-align:center;
        margin:0 auto 50px;
        .title{
            width:100%;
            margin:30px auto 20px;
            font-size:18px;
            text-align:center;
            font-weight:600;
        }
        .info-box{
            padding:60px 100px 0;
            text-align:left;
            .info-tit{
                padding:0 30px 20px;
                text-align:left;
                font-size:14px;
                font-weight:700;
            }
            .info{
                width:400px;
                text-align:center;
                padding:0 30px;
                li{
                    list-style:none;
                    width:100%;
                    height:45px;
                    text-align:left;
                    span{
                        display:inline-block;
                        font-size:14px;
                        font-weight:700;
                        line-height:45px;
                        padding-right:10px;
                        color:#525252;
                    }
                    p{
                        display:inline-block;
                        font-size:14px;
                        line-height:45px;
                        color:#808080;
                    }
                }
            }
        }
        .assets-info{
            margin:0 auto;
            padding:30px 100px 0;
            text-align:left;
            .el-tabs{
                .el-tab-pane{
                    .name{
                        font-size:14px;
                        margin:20px 0 16px;
                    }
                    .el-table{
                        margin:0 0 30px;
                        &:hover{
                            box-shadow:none;
                        }
                    }
                }
            }
        }
        .return-btn{
            margin:50px auto 0;
        }
    }
</style>