<template>
  <div class="user">
    <div class="user-search">
      <div class="input-box">
        <el-input placeholder="" v-model="searchContent" class="input-with-select"></el-input>
      </div>
      <el-select v-model="optionValue" placeholder="" class="account-select">
        <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
        </el-option>
      </el-select>
      <el-button type="primary" class="btn" @click="dataFilter"></el-button>
      <el-button type="primary" class="btn" @click=" resetFilter" plain></el-button>
      <el-button type="primary" class="new-btn" @click="goAccountAdd"></el-button>
    </div>
    <el-table
            highlight-current-row
            :data="accountTable"
            class="user-table1"
            style="width: 100%;padding:16px 24px;">
      <el-table-column
              prop="id"
              label="ID"
              min-width="135">
      </el-table-column>
      <el-table-column
              prop="userName"
              label=""
              min-width="70">
      </el-table-column>
      <el-table-column
              prop="nickname"
              label=""
              min-width="60">
      </el-table-column>
      <el-table-column
              prop="roleName"
              label=""
              min-width="65">
      </el-table-column>
      <el-table-column
              prop="status"
              label=""
              min-width="35">
      </el-table-column>
      <el-table-column
              prop="createdAt"
              label="">
      </el-table-column>
      <el-table-column
              label=""
              min-width="100">
        <template slot-scope="scope">
              <el-button size="small" type="primary" plain style="width:60px;height:30px;" @click='goAccountDetail(scope.row)'></el-button>
              <el-button size="small" type="primary"  style="width:60px;height:30px;" @click="goAccountEdit(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-box">
      <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[100, 200, 300, 400]"
              :page-size="pageSize"
              :total="totalSize"
              layout="prev, pager, next, jumper"
              v-if="totalSize > pageSize">
      </el-pagination>
      <div class="pagination-tail" v-if="totalSize > pageSize">{{Math.ceil(totalSize/pageSize)}}</div>
    </div>
  </div>
</template>

<script>
    import {mapState} from 'vuex'
    import util from '@/libs/util.js'
    import { mapActions } from 'vuex'
    export default {
      data() {
        return {
          searchContent: '',
          optionValue: '',
          currentPage: 1,
          pageSize:10,
          totalSize:'',
          status:'',
          userName:'',
          accountTable:[],
          options: [{
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }],
          allRoles: []
        }
      },
      computed:{
        ...mapState({
          userAuthority: state => state.d2admin.userAuthority
        })
      },
      watch:{
       '$route':{
         handler(val){
           this.getAccountList(this.currentPage,this.pageSize,this.userName,this.status);
         },
         deep:true
       }
      },
      methods: {
        //
        getAllRoles(){
          let vm = this;
          this.axiosPOST(vm, vm.$API.API.allRoles,
            'application/x-www-form-urlencoded',
            {},
            vm.getSuccessCB,
            vm.getErrorCB
          )
        },
        //
        getSuccessCB(vm,res){
          if(res.data){
            vm.allRoles = res.data;
          }
        },
        //
        getErrorCB(err){
          console.log(err)
        },
        //
        getAccountList(currentPage,pageSize,userName,status){
          let vm = this;
          this.axiosPOST(vm, vm.$API.API.accountList,
            'application/x-www-form-urlencoded',
            {currentPage,pageSize,userName,status},
            vm.getAccountSuccessCB,
            vm.getErrorCB
          )
        },
        //
        getAccountSuccessCB(vm,res){
          vm.totalSize = res.count;
          if(!res.data || res.data === ''){
            vm.accountTable = [];
          } else {
            vm.setAccountTableData(res.data)
          }
        },
        //
        setAccountTableData(_data) {
          let myData = [];
          for (let i = 0; i <_data.length; i++) {
            let obj = {};
            obj.id = _data[i].id;
            obj.userName = _data[i].userName;
            obj.createdAt = _data[i].createdAt;
            if(!_data[i].nickname || _data[i].nickname === ''){
              obj.nickname = '-'
            } else {
              obj.nickname = _data[i].nickname
            }
            if(!_data[i].roleName || _data[i].roleName === ''){
              obj.roleName = '-'
            } else {
              obj.roleName = _data[i].roleName
            }
            _data[i].status === 1 ? obj.status = '' : obj.status = '';
            myData[i] = obj;
          }
          this.accountTable = myData;
        },
        //
        goAccountDetail(row){
          // this.$router.push({path: '/authority/account/accountDetail', query: {id: row.id}})
          this.$router.push({
            name:'accountDetail',
            params:{
              id: row.id,
              roleName: row.roleName
            }
          })
        },
        //
        goAccountEdit(row){
          let tmpStatus = '';
          row.status === ''? tmpStatus = 1 : tmpStatus = 2;
          // this.$router.push({path: '/authority/account/accountEdit', query: {id: row.id}})
          this.$router.push({
            name:'accountEdit',
            params:{
              id: row.id,
              roleName: row.roleName
            }
          })
        },
        //
        goAccountAdd(){
          this.$router.push('/authority/account/accountAdd');
        },
        //
        handleCurrentChange(page) {
          this.currentPage = page;
          this.getAccountList(this.currentPage,this.pageSize,this.userName,this.status);
        },
        //
        handleSizeChange(val) {
          this.currentPage = 1;
          this.userName = '';
          this.status = '';
          this.getAccountList(this.currentPage,this.pageSize,this.userName,this.status);
        },
        //
        dataFilter(){
          this.currentPage = 1;
          this.userName = this.searchContent.toString();
          if(this.optionValue === ''){
            this.status = 1
          } else if(this.optionValue === '') {
            this.status = 2
          } else {
            this.status = ''
          }
          this.getAccountList(this.currentPage,this.pageSize,this.userName,this.status);
        },
        //
        resetFilter(){
          this.status = '';
          this.userName = '';
          this.currentPage = 1;
          this.searchContent = '';
          this.optionValue = '';
          this.getAccountList(this.currentPage,this.pageSize,this.userName,this.status);
        }
      },
      mounted(){
        this.getAccountList(this.currentPage,this.pageSize,this.userName,this.status);
      }
    }
</script>

<style scoped lang= "scss">
  @import '~@/assets/style/public-class.scss';
  .user {
    width:100%;
    margin:30px auto;

    .user-search{
      width:100%;
      margin:0 auto 30px;
      @include clearfix;
      .txt{
        float:left;
        display:inline-block;
        font-size:14px;
        line-height:40px;
      }
      .input-box {
        float:left;
        width: 220px;
        height: 40px;
        margin-right:20px;
      }
      .account-select {
        float:left;
        margin-right:20px;
      }
      .btn{
        float:left;
        width:100px;
        height:40px;
      }
      .new-btn{
        float:right;
      }
    }
    .recharge-pagination{
      margin:30px auto;
      text-align:center;
    }

  }
</style>