const utilIce = {}

/**
 * @description 
 * @param {Array} config 
 */
utilIce.recursiveRouterConfig = function recursiveRouterConfig (config = []) {
  const routerMap = []
  /**
   * path -> name
   * @param {String} path path
   */
  function path2name (path = '') {
    return path.split('/').filter(e => e).join('-')
  }
  /**
   * recursive
   * @param {Array} con config
   */
  function recursive (con) {
    con.forEach((item) => {
      const route = item.layout ? {
        // -> 
        // 
        path: item.path,
        // 
        component: item.layout,
        //    path = 
        children: [
          {
            //  
            path: '',
            //  name  path name
            name: item.name || path2name(item.path),
            // meta 
            meta: Object.assign({
              requiresAuth: true,
              title: path2name(item.path)
            }, item.meta),
            // 
            component: item.component
          }
        ]
      } : {
        // -> 
        // 
        path: item.path,
        //  name  path name
        name: item.name || path2name(item.path),
        // meta 
        meta: Object.assign({
          requiresAuth: true,
          title: path2name(item.path)
        }, item.meta),
        // 
        component: item.component
      }
      if (Array.isArray(item.children)) {
        recursive(item.children)
      }
      routerMap.push(route)
    })
    return routerMap
  }
  return recursive(config)
}

/**
 * @description 
 * @param {Array} arr menu config
 */
utilIce.recursiveMenuConfig = function recursiveMenuConfig (arr) {
  const res = []
  /**
   *  name  title
   * @param {Object} obj menu
   */
  function convert (obj) {
    const {
      name, path, icon, children
    } = obj
    return {
      title: name,
      icon,
      path,
      ...children ? { children: children.map(convert) } : {}
    }
  }
  arr.forEach((menu) => {
    res.push(convert(menu))
  })
  return res
}

export default utilIce
