<template>
    <d2-container>
        <template slot="header"><span>  </span><i class="el-icon-close detail-close" @click="$router.go(-1)"></i></template>
        <div class="account-edit">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="account-ruleForm">
                <el-form-item label="" prop="userName">
                    <el-input :placeholder="ruleForm.userName" v-model="ruleForm.userName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="phone">
                    <el-input :placeholder="ruleForm.phone" v-model="ruleForm.phone"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input type="password" placeholder="" v-model="ruleForm.password"></el-input>
                </el-form-item>
                <el-form-item label="" prop="nickname">
                    <el-input :placeholder="ruleForm.nickname" v-model="ruleForm.nickname"></el-input>
                </el-form-item>
                <el-form-item label="" prop="roleId">
                    <el-select v-model="editRoleName" placeholder="">
                        <el-option v-for="item in allRoles" :label="item.displayName+'('+item.name+')'" :value="item.displayName" ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="hint">
                    <el-input :placeholder="ruleForm.hint" v-model="ruleForm.hint"></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input :placeholder="ruleForm.email" v-model="ruleForm.email"></el-input>
                </el-form-item>
                <el-form-item label="" prop="status">
                    <span>
                        <el-radio v-model="editStatus" label=""></el-radio>
                        <el-radio v-model="editStatus" label=""></el-radio>
                    </span>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')" :loading="submitLoading"></el-button>
                    <el-button @click="getInfo"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </d2-container>
</template>

<script>
  import util from '@/libs/util.js'
  import validators from '@/assets/js/validator.js'
  export default {
    name: "accountEdit",
    data() {
      return {
        editRoleName:'',
        id:'',
        editStatus:'',
        ruleForm: {
          email:'',
          headurl: '',
          hint: '',
          nickname: '',
          password: '',
          phone: '',
          roleId:'',
          status: 1,
          userName: ''
        },
        rules: {
          userName: [
            { required: true, message: '', trigger: 'blur' },
          ],
          phone: [
            { validator:validators.checkPhone, trigger: 'change' }
          ],
          password: [
            { validator:validators.checkPwd, trigger: 'change' }
          ],
          nickname: [
            { required: false, message: '', trigger: 'change' }
          ],
          roleId: [
            { required: false, message: '', trigger: 'change' }
          ],
          hint: [
            { required: false, message: '', trigger: 'change' }
          ],
          email: [
            { validator:validators.checkEmail, trigger: 'change' }
          ]
        },
        allRoles:[],
        submitLoading: false
      }
    },
    watch:{
      'editStatus':function(val){
        if(val === ''){
          this.ruleForm.status = 1;
        } else {
          this.ruleForm.status = 2;
        }
      },
      'editRoleName':function(val){
        for(let i=0; i<this.allRoles.length; i++){
          if(val === this.allRoles[i].displayName){
            this.ruleForm.roleId = this.allRoles[i].id;
            return;
          }
        }
      }
    },
    methods: {
      //
      getAllRoles(){
        let vm = this;
        this.axiosPOST(vm, vm.$API.API.allRoles,
          'application/x-www-form-urlencoded',
          {},
          vm.getSuccessCB,
          vm.getErrorCB
        )
      },
      //
      getSuccessCB(vm,res){
        if(res.data){
          vm.allRoles = res.data;
        }
      },
      //
      getErrorCB(err){
        console.log(err)
      },
      //
      getInfo(){
        let vm = this;
        let id = vm.id;
        this.axiosGET(vm, vm.$API.API.accountInfo + id,
          'application/x-www-form-urlencoded',
          {},
          vm.getInfoSuccessCB,
          vm.getErrorCB
        )
      },
      //
      getInfoSuccessCB(vm,res){
        if(res.data){
          vm.setDefault(res.data)
        }
      },
      //
      setDefault(_data){
         this.ruleForm.email = _data.email;
         this.ruleForm.userName = _data.userName;
         this.ruleForm.hint = _data.hint;
         this.ruleForm.nickname = _data.nickname;
         this.ruleForm.phone =  _data.phone;
         this.ruleForm.status = _data.status;
         if(_data.status === 1){
           this.editStatus = ''
         } else {
           this.editStatus = ''
         }
        this.ruleForm.password = '';
        this.ruleForm.roleId = _data.roleId; //
        for(let i=0; i<this.allRoles.length; i++){
          if(_data.roleId === this.allRoles[i].id){
            this.editRoleName = this.allRoles[i].displayName
          }
        }
      },
      //
      submitForm(formName) {
        let vm = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            vm.submitDataAPI();
          } else {
            return false;
          }
        });
      },
      //
      submitDataAPI(){
        let vm = this;
        let id = vm.id;
        vm.$nextTick(() => {
          let token = util.cookies.get('token');
          vm.$axios({
            method: 'put',
            headers: {
              'Content-Type': 'application/json',
              'x-auth-token': token
            },
            url: vm.$API.URL + vm.$API.API.accountEdit+id,
            data: vm.ruleForm
          })
            .then((res) => {
              if(res.code === '3001'){
                vm.$message.error('')
                util.cookies.remove('token');
                util.cookies.remove('uuid');
                util.cookies.remove('d2adminMenuAside');
                vm.$router.push({
                  name: 'login'
                })
              } else if(res.code === '3002'){
                vm.$message.error('')
                setTimeout(function(){
                  vm.$router.push('/authority/account');
                },300)
              } else if (res.code === '1000') {
                vm.submitLoading = true;
                vm.$message({
                  message: '',
                  type: 'success'
                });
                setTimeout(function(){
                  vm.$router.replace('/authority/account');
                },300)
              } else {
                vm.$message.error(res.message)
              }
            })
            .catch((err) => {
              console.log(err)
            })
        })
      },
    },
    mounted(){
      this.id = this.$route.params.id;
      this.getAllRoles();
      this.getInfo();
    }
  }
</script>

<style scoped lang="scss">
    .account-edit{
        width:100%;
        margin:20px auto;
        text-align:left;
        .account-ruleForm{
            width:500px;
        }
    }
</style>