<template>
  <div class="user">
    <div class="user-search">
      <div class="txt"></div>
      <div class="input-box">
        <el-input placeholder="" v-model="searchContent" class="input-with-select">
          <!--<el-button slot="append" icon="el-icon-search" @click="filterTable"></el-button>-->
        </el-input>
      </div>
      <el-button type="primary" class="btn" @click="filterTable"></el-button>
      <el-button type="primary" class="btn" @click="resetTable" plain></el-button>
      <el-button type="primary" class="black-btn" @click="onlyBlackList"></el-button>
    </div>
    <el-table
            highlight-current-row
            :data="userTable"
            :cell-class-name="getCellClass"
            class="user-table1"
            style="width: 100%;padding:16px 24px;">
      <el-table-column
              prop="id"
              label="ID"
              width="180">
      </el-table-column>
      <el-table-column
              prop="mobile"
              label=""
              width="180">
      </el-table-column>
      <el-table-column
              prop="nickName"
              label="">
      </el-table-column>
      <el-table-column
              prop="createTime"
              label="">
      </el-table-column>
      <el-table-column
              label="">
        <template slot-scope="scope">
              <el-button size="small" style="width:60px;height:30px;" @click="goUserDetailPage(scope.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column
              prop="status"
              label="">
              <template slot-scope="scope">
                <span style="cursor:pointer;" @click="statusHandle(scope.row)">{{scope.row.status}}</span>
              </template>
      </el-table-column>
    </el-table>
    <div class="pagination-box">
      <el-pagination
              background
              @current-change="handleCurrentChange"
              :current-page="pageNow"
              :page-size="pageSize"
              :total="totalSize"
              layout="prev, pager, next, jumper"
              v-if="totalSize>pageSize">
      </el-pagination>
      <div class="pagination-tail" v-if="totalSize>pageSize">{{Math.ceil(totalSize/pageSize)}}</div>
    </div>
  </div>
</template>

<script>
    import util from '@/libs/util.js'
    export default {
        data(){
            return {
                searchContent:'',
                pageNow:1,
                pageSize:10,
                totalSize:'',
                phone:'',
                status:'',
                userTable:[]
            }
        },
        methods: {
            //
            getCellClass({row, column, columnIndex}) {
                if (columnIndex == 5 && row[column.property].indexOf('') > -1) {
                    return 'active-gray'
                } else if(columnIndex == 5 && row[column.property].indexOf('') > -1){
                    return 'active-blue'
                } else {
                    return ''
                }
            },
            //
            getUserList(pageNow,pageSize,phone,status){
              let vm = this;
              this.axiosPOST(vm, vm.$API.API.userList,
                'application/x-www-form-urlencoded',
                { pageNow, pageSize, phone, status },
                vm.getUserListSuccessCB,
                vm.getUserListErrorCB
              )
            },
            //
            getUserListSuccessCB(vm,res){
              vm.totalSize = res.count;
              if(!res.data || res.data === ''){
                vm.userTable = [];
              } else {
                vm.setUserTableData(res.data);
              }
            },
            //
            getUserListErrorCB(err){
              console.log(err)
            },
            //
            setUserTableData(_data) {
            let myData = [];
            for (let i = 0; i <_data.length; i++) {
              let obj = {};
              obj.id = _data[i].id;
              obj.mobile = _data[i].mobile;
              obj.createTime = _data[i].createTime;
              if(!_data[i].nickName || _data[i].nickName === ''){
                obj.nickName = '-'
              } else {
                obj.nickName = _data[i].nickName
              }
              _data[i].status === 0 ? obj.status = '' : obj.status = '';
              myData[i] = obj;
            }
            this.userTable = myData;
          },
            //
            statusHandle(row){
                let id = row.id;
                let status = '';
                row.status === '' ? status = 1 : status = 0;
                this.changeStatus(id,status)
            },
            //
            changeStatus(userId,status){
              let vm = this;
              this.axiosPOST(vm, vm.$API.API.userStatus,
                'application/x-www-form-urlencoded',
                {userId,status},
                vm.getStatusSuccessCB,
                vm.getErrorCB
              )
            },
            //
            getStatusSuccessCB(vm,res){
              vm.getUserList(vm.pageNow, vm.pageSize, vm.phone, vm.status);
            },
            //
            handleCurrentChange(page) {
              this.pageNow = page;
              this.getUserList(this.pageNow, this.pageSize, this.phone, this.status);
            },
            // - 
            onlyBlackList(){
              this.pageNow = 1;
              this.phone = '';
              this.status = 0;
              this.getUserList(this.pageNow,this.pageSize,this.phone,this.status);
            },
            // - 
            filterTable(){
              this.pageNow = 1;
              this.status = '';
              this.phone = this.searchContent;
              this.getUserList(this.pageNow,this.pageSize,this.phone,this.status);
            },
            //
            resetTable(){
              this.pageNow = 1;
              this.phone = '';
              this.status = this.searchContent = '';
              this.getUserList(this.pageNow,this.pageSize,this.phone,this.status);
            },
            //
            goUserDetailPage(row){
              this.$router.push({
                name: 'userDetail',
                params: {
                  id: row.id,
                  nickname:row.nickName,
                  mobile: row.mobile,
                  createTime: row.createTime
                }
              })
            }
        },
        mounted(){
          this.getUserList(this.pageNow, this.pageSize, this.phone, this.status);
        }
    }
</script>

<style scoped lang= "scss">
  @import '~@/assets/style/public-class.scss';
  .user {
    width:100%;
    margin:30px auto;

    .user-search{
      width:100%;
      margin:0 auto 30px;
      @include clearfix;
      .txt{
        float:left;
        display:inline-block;
        font-size:14px;
        line-height:40px;
      }
      .input-box {
        float:left;
        width: 250px;
        height: 40px;
        margin:0 20px;
      }
      .btn{
        float:left;
        width:100px;
        height:40px;
      }
      .black-btn{
        float:right;
        width:120px;
        height:40px;
      }
    }
    .recharge-pagination{
      margin:30px auto;
      text-align:center;
    }

  }
</style>